import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			bitmap: true,
			page:1,
			loading:false,
			rechargeList:[],
		}
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			})
		},
		/**
		 * 账单列表
		 */
		rechargeLista() {
		  var that = this;
		  var page = 1; 
		  var num = 15;
		  var timeStamp = Math.round(new Date().getTime() / 1000);
		  var obj = { appid: appid, timeStamp: timeStamp, }
		  var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		  var data = { appid: appid, timeStamp: timeStamp, sign: sign, page: 1, num: 15 }
		  rs.getRequest("rechargeList", data, (res) => {
		    if (res.data.code == 200) {
				if(res.data.data != ''){
					that.rechargeList = res.data.data;
					that.bitmap = false;
					if(res.data.data.length<=10){
						that.loading = false;
					}
					
				}else{
					that.loading = '空';
					that.bitmap = true;
				}
		    }
		  })
		},
	},
	onShow() {
		var that = this;
		that.rechargeLista()
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function () {
	  var that = this;
	  var page = that.page;
	  var num = 15;
	  var timeStamp = Math.round(new Date().getTime() / 1000);
	  var obj = { appid: appid, timeStamp: timeStamp, }
	  var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
	  var data = { appid: appid, timeStamp: timeStamp, sign: sign, page:page+ 1, num: 15 }
	  rs.getRequest("rechargeList", data, (res) => {
	    if (res.data.code == 200) {
			if(res.data.data != ''){
				for(var i=0;i<res.data.data.length;i++){
					that.rechargeList.push(res.data.data[i]);
				}
				that.loading = true;
				that.bitmap = false;
				that.page = page+1;
			}else{
				that.loading = false;
			}
	    }
	  })
	
	},
}
